import { NextRequest, NextResponse } from "next/server";

const NEST_API_URL = process.env.NEST_API_URL || "http://localhost:8080";

export async function POST(req: NextRequest) {
  try {
    const body = await req.json();
    const { userId, assistant_id, promt } = body;

    if (!userId || !assistant_id || !promt) {
      return NextResponse.json(
        {
          success: false,
          error:
            "Missing required fields: userId, assistant_id, and promt are required",
          received: body,
        },
        { status: 400 }
      );
    }

    const response = await fetch(`${NEST_API_URL}/chat/start`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(body),
    });

    const data = await response.json();

    return NextResponse.json(data, { status: response.status });
  } catch (error) {
    return NextResponse.json(
      { success: false, error: "Internal server error" },
      { status: 500 }
    );
  }
}
